.addBtn{
    position: fixed;
    top:80vh;
    right: 20vw;
    border: none;
    background-color: rgba(170, 240, 199, 0.808);
    border-radius: 10rem;
    width: 5rem;
    height: 5rem;
    box-shadow: 0px 0px 30px 1px rgba(170, 240, 199, 0.904);
}
.addBtn:active{
    box-shadow: none;
}

.dataInput{
    outline-style: none;
    margin: 1rem;
    width: 20vh;
    border-radius: 10rem;
    border-color: gold;
    padding: 0rem 1rem 0px 1rem;
}
table,tr,td{
    border: 1px solid rgb(123, 255, 193);
}
table{

    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
thead{
    font-weight: bold;
    background-color: rgba(105, 158, 158, 0.432);
}
h1{
    font-family: 'Black Ops One', cursive;
     text-align: center;
}
@media only screen and (max-width:480px){
    .dataInput{
        width: 70vw;
        height: 7vh;
    }
    table{
        font-size: 0.5rem;
    }
    .opBtn{
        font-size: 0.5rem;
        width: 3rem;
    }
    #plusSign{
        width: 2rem;
        height: 2rem;
    }
    .addBtn{
        top:70vh;
        right: 10vw;
        width: 3rem;
        height: 3rem;
    }
}